.quantity{
  .dropdown,
  .dropdown div,
  .dropdown li,
  .dropdown div::after{
    -webkit-transition: all 150ms ease-in-out;
    -moz-transition: all 150ms ease-in-out;
    -ms-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
  }

  .dropdown .selected::after,
  .dropdown.scrollable div::after{
    -webkit-pointer-events: none;
    -moz-pointer-events: none;
    -ms-pointer-events: none;
    pointer-events: none;
  }

  /* WRAPPER */

  .dropdown{
    position: relative;
    width: 63px;
    border-bottom: 2px solid #ffffff;
    cursor: pointer;
    background: transparent;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
  }

  .dropdown.open{
    z-index: 2;
  }


  /* CARAT */

  .dropdown .carat{
    position: absolute;
    right: 12px;
    top: 50%;
    margin-top: -4px;
    border: 6px solid transparent;
    border-top: 8px solid #fff;
  }

  .dropdown.open .carat{
    margin-top: -10px;
    border-top: 6px solid transparent;
    border-bottom: 8px solid #fff;
  }

  .dropdown.disabled .carat{
    border-top-color: #999;
  }

  /* OLD SELECT (HIDDEN) */

  .dropdown .old{
    position: absolute;
    left: 0;
    top: 0;
    height: 0;
    width: 0;
    overflow: hidden;
  }

  .dropdown select{
    position: absolute;
    left: 0px;
    top: 0px;
  }

  .dropdown.touch .old{
    width: 100%;
    height: 100%;
  }

  .dropdown.touch select{
    width: 100%;
    height: 100%;
    opacity: 0;
  }

  /* SELECTED FEEDBACK ITEM */

  .dropdown .selected,
  .dropdown li{
    display: block;
    font-size: 18px;
    line-height: 1;
    color: #fff;
    padding: 9px 12px;
    overflow: hidden;
    white-space: nowrap;
    outline: none;
    width: 33px;
    margin-left: 6px;
  }

  .dropdown.disabled .selected{
    color: #999;
  }

  .dropdown .selected::after{
    content: '';
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 60px;

  }

  /* DROP DOWN WRAPPER */

  .dropdown div{
    position: absolute;
    height: 0;
    right: -1px;
    top: 100%;
    background: rgba(182, 77, 112, 0.6);
    overflow: hidden;
    opacity: 0;
    left: -31px;
    width: 123px;
  }

  /* Height is adjusted by JS on open */

  .dropdown.open div{
    opacity: 1;
    z-index: 2;
    margin-top: 2px;
  }

  /* FADE OVERLAY FOR SCROLLING LISTS */

  .dropdown.scrollable div::after{
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 50px;
  }

  .dropdown.scrollable.bottom div::after{
    opacity: 0;
  }

  /* DROP DOWN LIST */

  .dropdown ul{
    position: absolute;
    left: 0;
    top: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    list-style: none;
    overflow: hidden;
  }

  .dropdown.scrollable.open ul{
    overflow-y: auto;
  }

  /* DROP DOWN LIST ITEMS */

  .dropdown li{
    list-style: none;
    padding: 8px 12px;
  }

  /* .focus class is also added on hover */

  .dropdown li.focus{
    background: #d24a67;
    position: relative;
    z-index: 3;
    color: #fff;
  }

  .dropdown li.active{
    font-weight: 700;
  }
}